

@font-face {
  font-family: 'Pacifico';
  src: local('Pacifico'), url(./Pacifico-Regular.ttf) format('truetype');
}

*{padding: 0; margin: 0; box-sizing: border-box;}
body{height: 900px; background-color: #fffffe;}
header {
  /*background: url('http://www.autodatz.com/wp-content/uploads/2017/05/Old-Car-Wallpapers-Hd-36-with-Old-Car-Wallpapers-Hd.jpg');*/
  text-align: center;
  width: 100%;
  height: auto;
  background-size: cover;
  background-attachment: fixed;
  position: relative;
  overflow: hidden;
  background-color: #094067;
  /*border-radius: 0 0 85% 85% / 30%;*/
}
header .overlay{
  /*width: 100%;*/
  width: 100%;
  height: 100%;
  padding: 50px;
  color: #FFF;
  /*text-shadow: 1px 1px 1px #333;*/
  font-family: 'Pacifico', cursive;
  /*background-image: linear-gradient( 135deg, #9f05ff69 10%, #fd5e086b 100%);*/
  
}

h1 {
  font-family: 'Pacifico', cursive;
  font-size: 7em;
  /*margin-bottom: 30px;*/
  color: #fffffe;
  text-decoration: underline;
  margin-bottom: 35px;
}

h3, p {
  font-family: 'Quicksand', sans-serif;
  font-size: 2rem;
  text-decoration: underline;
  color: #fffffe;

  /*margin-bottom: 30px;*/
}
.button {
  font-family: 'Quicksand', sans-serif;
  border: none;
  outline: none;
  color: #fffffe;
  /*background: #fff;*/
  background-color: #3da9fc;
  margin-top: 15px;
  margin-bottom: 15px;
  
}

.howto {
  padding: 10px 20px;
  border-radius: 50px;
  margin-bottom: 30px;

}

.howto:hover {
  color: #fffffe;
  background-color: #5A6268;
}

.type {
  margin-right: 20px;
  margin-left: 20px;
}

@media screen and (max-width: 375px) {
  .sorttype {
    margin-left: 0px;
    margin-right: 0px;
  }
    body {
      width: 379px;
  }
}

h4 {
  font-family: 'Quicksand', sans-serif;
  display: inline-block;
  color: #5f6c7b;

}

.button:hover{
  cursor: pointer;
}


.sorttype {
  margin-top: 0px;
  margin-bottom: 0px;
}

.card {
  width: 18rem;
  margin-top:20px;
  font-family: 'Quicksand', sans-serif;
}

.scrollable-menu {
  height: auto;
  max-height: 200px;
  overflow-x: hidden;
}

h5 {
  font-family: 'Quicksand', sans-serif;
  font-size: 4em;
  margin-top:15px;
  color: #5f6c7b;
  font-weight: bold;
}

.hd {
  display: inline-flex;
  font-size:2em;
}

.card-font {
  font-family: 'Quicksand', sans-serif;
}

.view {
  position:absolute;
    right:0;
    margin-right: 20px;
    /*margin-top:10px;*/
}

.grid {
  margin-right: 7px;
}

.list {
  margin-left: 7px;
}

.page-link.active {
  color: #fffffe;
  background-color: #3DA9FC;
}

